window.onload = () => {
  document.onwheel = customScrollFunction;

  function customScrollFunction(event) {
    let deltaY = event.deltaY;
    let deltaYSign = Math.sign(deltaY);
    let vwDistance = document.getElementById("scrl1").clientWidth;
    if (deltaYSign == -1) {
      document.getElementById("scrl1").scrollBy({
        top: 0,
        left: vwDistance * -0.25,
        behavior: "smooth",
      });
      console.log(deltaY);
    } else {
      document.getElementById("scrl1").scrollBy({
        top: 0,
        left: vwDistance * 0.25,
        behavior: "smooth",
      });
      console.log(deltaY);
    }
  }
};
